body {
  background-color: #282c34;
  height: fit-content;
  margin: auto;
}

.App-header {
  margin-top: 50px;
  font-size: 16px;
  width: auto;
  color: white;
}

.tab {
  cursor: pointer;
}

#players, #duelos {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
}

#players td, #players th {
  padding: 8px;
}

#duelos td, #duelos th {
  border: 1px solid #ddd;
  padding: 8px;
}

#players tr:nth-child(even){background-color: #424242;}

#duelos tr:nth-child(even){background-color: #424242;}

#players th, #duelos th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #4CAF50;
  color: white;
}


@media only screen and (max-width: 1024px) {
  .table-content {
    overflow-x: scroll;
  }

  .App-header {
    font-size: 12px;
  }
}